* {
    box-sizing: border-box;
    font-family: "Open Sans";
}

body {
    padding: 0;
    margin: 0;
    overflow-y: scroll;
}

.content {
    min-height: 80vh;
    overflow: hidden;
    position: relative;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background: #888;
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}

a, a:visited, a:hover, a:focus {
    text-decoration: none;
    color: inherit;
}

.nav {
    background-color: #17181C;
    color: #ddd;
    font-family: 'Montserrat';
    font-weight: 600;
    position: sticky;
    display: block;
    height: 90px;
    letter-spacing: 2px;
    font-size: 14px;
    top: 0;
    z-index: 100;
}

.nav img {
    width: 120px;
    display: inline-block;
    margin: 10px;
}

.nav ul {
    display: table;
    list-style: none;
    padding: 0;
    float: right;
    margin: 0;
    height: 100%;
    padding-right: 20px;
}

.nav li {
    vertical-align: middle;
    padding: 5px 5px;
    display: table-cell;
    transition: all 0.1s ease-in-out;
}

.nav li.active {
    background-color: #B37113 !important;
}

.nav li a {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0px 25px;
}

.nav li:hover {
    background-color: #313236;
}

.back-img {
    position: relative;
    height: 80vh;
    overflow: hidden;
}

.back-img img {
    width: 100%;
    margin-top: -10%;
    display: block;
}

.back-img-text {
    position: absolute;
    color: #FDFFE0;
    z-index: 50;
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    text-align: center;
    font-family: 'Josefin Sans';
}

.back-img-text.head {
    top: 100px;
    font-size: 50px;
    font-weight: 700;
}

.back-img-text.desc {
    top: 180px;
    font-size: 30px;
    font-weight: 700;
}

.back-img-text.desc-2 {
    top: 400px;
    font-size: 25px;
    font-weight: 500;
    color: #ccc;
    line-height: 40px;
}

.footer {
    background-color: #17181C;
    padding: 20px;
    text-align: center;
    color: #888;
    font-family: "Arial";
    font-size: 14px;
    z-index: 100;
    display: block;
    position: relative;
}